@import 'https://fonts.googleapis.com/css?family=Playfair+Display:400,400i,700,700i,900,900i|Poppins:300,400,500,600,700';


*
{
	box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

body{
  background-color: #f0f0f0;
  color: #222222;
  font-family: 'Poppins';
}
.hero{
  background: transparent;
  height: 100vh;
}
.sheet{
  width: 60%;
  height: 80%;
  max-width: 1024px;
  max-height: 800px;
  background-color: #fff;
  position: absolute;
  margin: auto;
  top:0; left: 0;right: 0;bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: flex-row;
          flex-direction: flex-row;

  -webkit-transform-style: preserve-3d;

          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;

  -webkit-transition: box-shadow 0.1s ease;

  transition: box-shadow 0.1s ease;
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: all .2s ease;
  box-shadow: 0 4px 30px rgba(0,0,0,0.10), 0 6px 60px rgba(0,0,0,0.10);

  &:hover {
      box-shadow: 0 4px 15px rgba(0,0,0,0.05), 0 6px 30px rgba(0,0,0,0.05);
      -webkit-transition: all 0.1s ease;
      transition: all .2s ease;

  }

  /* &:active {
    box-shadow: 0 2px 20px rgba(0,0,0,0.20), 0 6px 30px rgba(0,0,0,0.10);
      transition: box-shadow 0.1s ease;
  } */
}

.left{
  position: relative;
  width: 50%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column
}

.left p {
    margin: 0;
    color: #FFFFFF;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;

 }

.title{
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1.8px;
  }
.meta{
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1.1px;
}
.right{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 30px;
  background: white;
  z-index: 0;
  right: 0;
}
.right h1{
  font-family: 'Playfair display';
  font-weight: 900;
  font-size: 10vh;
  line-height: 10vh;
  position: absolute;
  margin-top: 5vh;
  width: 40vw;
  max-width: 800px;
  -webkit-transform: translateZ(10px);
          transform: translateZ(10px);
}

.right h2{
  font-family: 'Poppins';
  font-size: 24px;
  color: #323232;
  line-height: 120%;
  -webkit-transform: translateZ(5px);
          transform: translateZ(5px);

}
.right a{
  position: absolute;
  bottom: 25px;
  color: currentColor;
  font-weight: bold;
  text-decoration: none;
  border-bottom: 2px solid currentColor;
  line-height: 230%;
  -webkit-transition: line-height 0.1s ease;
  transition: line-height 0.1s ease;
}

.right a:hover {
  }

#slider {
  position: absolute;
  overflow: hidden;
  background-color: #222;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  top: 0;
  -webkit-box-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
}

#slider ul {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}


/* make keyframes that tell the start state and the end state of our object */
@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
@-moz-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
@keyframes fadeIn { from { opacity:0; } to { opacity:1; } }

.fade-in {
  opacity:0;  /* make things invisible upon start */

}
#slider ul li {
  opacity: 0;
  -webkit-animation:fadeIn ease-in 1;  /* call our keyframe named fadeIn, use animattion ease-in and repeat it only 1 time */
  -moz-animation:fadeIn ease-in 1;
  animation:fadeIn ease-in 1;

  -webkit-animation-fill-mode:forwards;
  -moz-animation-fill-mode:forwards;
  animation-fill-mode:forwards;

  -webkit-animation-duration:1s;
  -moz-animation-duration:1s;
  animation-duration:1s;

  -webkit-animation-delay: 0.5s;
  -moz-animation-delay: 0.5s;
  animation-delay: 0.5s;

  position: relative;
  display: block;
  width: auto;
  height: auto;
  background-size: cover;
  background-position: center center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding: 60px 30px;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;

}
#slider ul li a{
  z-index: 9;
  width: 100%;
  height: 100%;
  flex-direction: column;
    justify-content: flex-end;
    display: flex;
  text-decoration: none;
  min-height: 100vh;
}
#slider ul li:after{
  cursor: pointer;
  content:'';
  position: absolute;
      z-index: 1;
    top: 0;left: 0;right: 0;bottom: 0;
width: auto;
  height: 100%;
    margin: auto;
  		 background-image: url(data:image/svg+xml;base64,);

  background-repeat: no-repeat;
  background-position: center center;
  opacity:0;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  background-size: fit;
  pointer-events: none;
}

#slider ul li:hover::after{
  opacity: 0.3;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

#slider ul li:active::after{
  opacity: 0.5;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}



#slider ul li::before {
  content: '';
  position: absolute;
  top: 0;left: 0;right: 0;bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  transition: background-color 0.2s ease;

}
#slider ul li:hover:before {
  background-color: rgba(0,0,0,0.65);
  transition: background-color 0.2s ease;
}

#slider ul li p{
  z-index: 2;
}

a.control_prev, a.control_next {
  position: absolute;
  bottom: 15px;
  z-index: 999;
  display: block;
  padding: 4% 5%;
  width: auto;
  height: auto;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  font-size: 18px;
  opacity: 0.8;
  cursor: pointer;
}

a.control_prev:hover, a.control_next:hover {
  opacity: 1;
  -webkit-transition: all 0.2s ease;
}

a.control_prev {
  left: 0px;
  	background-image: url(data:image/svg+xml;base64,);
background-repeat:no-repeat;
  background-position: center right;
      }

a.control_next {
  left:75px;
}
a.control_next {
  		background-image: url(data:image/svg+xml;base64,);
background-repeat:no-repeat;
  background-position: center left;
      }

.slider_option {
  position: relative;
  margin: 10px auto;
  width: 160px;
  font-size: 18px;
}

a.contactButton{
  right: 30px;
  opacity: .3;
  border-width: 0px;
  transition: opacity 0.2s ease;
  &:hover{
    opacity: 1;
    transition: opacity 0.2s ease;
  }
}

//* MEDIA QUERIES *//
@media screen and (max-width: 1024px) {
		.sheet{
			max-width: none;
      max-height: none;
      height: 100%;
      width: 100%;
			box-shadow: none;
		}
		.right h2{
			display: none;
		}
    .right h1{
      font-size: 11vh;
      line-height: 11vh;
      max-width: 650px;
			bottom: 20%;
    }
		.left{
			display:none;
		}

	}
